.cust-dropdown .dropdown-toggle.btn-group {
    padding-right: 12px;
}

.cust-dropdown .dropdown-toggle::after {
    position: absolute;
    right: 0;
    top: 41%;
    border: none;
    width: 10px;
    height: 10px;
    background: url(data:image/png;base64,) no-repeat center;
}

.custom-radio .custom-control-input:checked~.custom-control-label::before {
    color: #fff;
    border-color: #f7c6cf;
    background-color: #f7c7cf;
}

.custom-control-input:checked ~ .custom-control-label::before ,
.custom-control-input:focus ~ .custom-control-label::before,
.custom-control-input:focus:not(:checked) ~ .custom-control-label::before{
    box-shadow: none !important;
    outline: 0 !important;
}

.cust-dropdown.show.dropdown .dropdown-toggle::after {
    background: url(data:image/png;base64,) no-repeat center;
}

.cust-dropdown .dropdown-toggle::after,
.cust-dropdown.dropdown-toggle img {
    cursor: pointer;
}

