/* sidebar */
.page__sidebar {
    width: 331px;
}

.page__sidebar .sidebar-nav {
    display: block;
    padding: 0;
    transition: all 0.5s ease;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    border-radius: 15px;
    background: #fff;
    padding: 30px 49px;
    height: 100%;
}

@keyframes smoothScroll {
    0% {
        transform: translateY(-40px);
    }

    100% {
        transform: translateY(0px);
    }
}

.page__sidebar a.logo {
    display: block;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-bottom: 63px;
}

.page__sidebar .navbar-nav {
