/* add new goal */
.setnewgoal-view .cust-head-center-wrap .link-btn-wrap {
    position: absolute;
    right: 0;
    top: 0;
}


.newgoal-timeline {
    padding-top: 20px;
}

.newgoal-timeline,
.counter-timeline,
.countersteps-wrap {
    position: relative;
}

.timeline-row::after,
.counter-timeline::after {
    position: absolute;
    content: "";
    left: 24px;
    top: 0;
    margin: auto;
    width: 2px;
    background: #f8c8d0;
    height: 100%;
}

.counter-timeline:last-child::after,
.timeline-row:last-child::after {
    display: none;
}

.timeline-row .counter,
.counter-timeline .counter,
.counter {
    position: absolute;
    background: #f8c8d0;
    border: 2px solid transparent;
    width: 50px;
    height: 50px;
    display: block;
    text-align: center;
    line-height: 46px;
    border-radius: 50%;
    font-size: 20px;
    color: #fff;
    left: 0;
    top: -8px;
    z-index: 9;
    font-weight: 700;
}

.disabled-counter {
    background: #b9b7ba;
}

.newgoal-timeline h2 {
    font-size: 24px;
    line-height: 37px;
    font-weight: 700;
    padding-left: 0;
    margin-bottom: 18px;
}

