    max-height: 85vh;
    min-height: 100vh;
    height: 100%;
    background-color: #FDFDFD;
    border-radius: 15px;
    overflow: hidden;
}

.main-sign-process .sign-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    width: 70%;
    height: 100%;
    padding: 25px;
    background-color: #fdfdfd;
    transition: 1.25s;
}

.main-sign-process .form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

/**/
.main-sign-process .a-container {
    z-index: 100;
    left: calc(100% - 70%);
}

.main-sign-process .b-container {
    left: calc(100% - 70%);
    z-index: 0;
}

.main-sign-process .switch {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 30%;
    padding: 50px;
    z-index: 200;
    transition: 1.25s;
    background-image: linear-gradient(45deg, #f8c8d0, #f8c8d0);
    overflow: hidden;
}

.switch-heading,
.switch-img,
.switch-description,
.switch-btn {
    transition: all 1s ease;
}

.main-sign-process .switch__circle {
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 50%;
    background-image: linear-gradient(-45deg, #f8c8d0, #f8c8d0);
    bottom: -60%;
    left: -60%;
    transition: 1.25s;
}

.main-sign-process .switch__circle--t {
    top: -30%;
    left: 60%;
    width: 300px;
    height: 300px;
}

.main-sign-process .switch__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    width: 100%;
    padding: 50px 55px;
    transition: 1.25s;
}

.main-sign-process .switch__button:hover {
    transform: scale(1.015);
    transition: 0.15s;
}

.main-sign-process .switch__button:active {
    transform: scale(0.9);
    transition: 0.15s;
}

.main-sign-process .is-txr {
    left: calc(100% - 30%);
    transition: 1.25s;
    transform-origin: left;
}

.main-sign-process .is-txl {
    left: 0;
    transition: 1.25s;
    transform-origin: right;
}

.main-sign-process .is-z200 {
    z-index: 200;
    transition: 1.25s;
}

.main-sign-process .is-hidden {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    transition: 1.25s;
}

.main-sign-process .is-gx {
    animation: is-gx 1.25s;
}

@keyframes is-gx {

    0%,
    10%,
    50%,
    100% {
        width: 30%;
    }

    30%,
    40%,
    50% {
        width: 55%;
    }
}


